import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

import UserList from '../components/UserList';

export default class UserListContainer extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      users: [],
      loading: false,
      page: 1
    };
  }

  componentWillMount() {
    this.setState({
      loading: true
    });

    fetch(`https://jsonplaceholder.typicode.com/users?_limit=2&_page=1`)
      .then(res => res.json())
      .then(users => {
        this.setState({
          loading: false,
          users
        });
      });
  }

  handleDelete = (id) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        page: prevState.page + 1,
        users: prevState.users.filter(user => user.id !== id)
      };
    });
    this.loadNext();
  }

  loadNext = () => {
    this.setState({
      loading: true
    });
    const { page } = this.state;
    fetch(`https://jsonplaceholder.typicode.com/users?_limit=1&_page=${page}`)
      .then(res => res.json())
      .then(users => {
        this.setState((prevState) => {
          return {
            ...prevState,
            users: prevState.users.concat(users),
            loading: false
          }
        });
      });
  }

  render() {
    const { loading, users, pressed } = this.state;
    return (
      <div>
        { pressed && '' }
        { loading ? ' ' : <UserList onDelete={this.handleDelete} users={users} /> }
      </div>
    );
  }
}